<template>
    <div class="loginStyle">
        <div id="login-wrapper">
        <div class="flexContainer">
            <div style="flex:1"></div>
            <div class="loginContentStyle">
                <div id="login-box">
                    <img id="topleft-logo" class="img-responsive" src="../../../static/ensaas_login/img/wise-paas-logo.svg" />
                     <div class="switch">
                        <el-radio-group v-model="agent_switch" size="mini">
                            <el-radio-button label="qrcode">
                                <i class="fa fa-qrcode m-r-5" aria-hidden="true"></i>
                                {{$t('login.deviceRegistration')}}
                            </el-radio-button>
                            <el-radio-button label="login">
                                <i class="fa fa-user-circle-o m-r-5" aria-hidden="true"></i>
                                {{$t('login.userLogin')}}
                            </el-radio-button>
                        </el-radio-group>
                    </div>
                    <div id="center-logo-wrapper">
                        <!-- <img id="center-logo" class="img-responsive" src="@/assets/imgs/EIDevice.png"> -->
                        <span class="logo-icon">
                            AppHub Edge
                        </span>
                    </div>
                    <div v-if="agent_switch=='login'">
                        <div id="login-message">{{$t('login.subTitle')}}</div> 
                        <div id="login-form" ref="form">
                            <input id="login-username" type="text" class="form-control" placeholder="Username" maxlength="253" v-model="form.username">
                            <input id="login-pwd" type="password" class="form-control" placeholder="Password" minlength="8" v-model="form.password"
                            maxlength="20" @keyup.enter="handleSubmit()">
                        </div>
                        <label id="login-rememberme">
                            <input type="checkbox" v-model="checked">
                            <span class="noselect">{{$t('login.remember')}}</span>
                        </label>
                        <div id="login-btn" @click="handleSubmit()" v-loading="loading">
                            <span class="noselect">{{$t('login.login')}}</span>
                        </div>
                    </div>
 
                    <div class="qrcode" v-if="agent_switch=='qrcode'">
                        <div v-if="qrIsShow">
                            <qr-code :value="qrCodeValue" :options="{ width: 210}" class="block-center" ></qr-code>
                            <p class="qrIntroduce">
                                {{$t('login.qrcodeInfo')}}
                            </p>
                        </div>
                        <div class="input-config url"  @click="dialogConfigVisible=true">
                            <i class="fa fa-hand-o-right m-r-5 c-primary" aria-hidden="true"></i> 
                            {{$t('login.inputServerInformation')}}
                        </div> 
                        <p class="server">
                            <span class="server-label">{{$t('login.serverAddress')}}</span>
                            <span class="server-value">{{serverip}}</span>
                        </p> 
                    </div> 
                    <div class="agentVersion">
                        {{$t('login.agentVersion')}} {{agentVersion}}&nbsp;|&nbsp;{{deviceName}}&nbsp;|&nbsp;
                        <i class="fa fa-circle" style="color: green; font-size:17px" aria-hidden="true" v-if="agentStatus == 'online'"></i>
                        <i class="fa fa-circle c-darkgray" style="font-size: 17px" aria-hidden="true" v-else></i>
                    </div>      
                </div>
            </div>
            <el-dialog :visible.sync="dialogConfigVisible" :close-on-click-modal="false" v-loading="configLoading">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-cog m-r-10" aria-hidden="true"></i> 
                    {{$t('login.serverInfomation')}}
                </div>
                <el-form :model="configForm">
                    <el-form-item :label="$t('login.inputInformation')" label-width="210px" prop="configValue">
                        <el-input type="textarea" class="max-w-550" 
                        v-model="configForm.configValue" autocomplete="off"
                        :placeholder="$t('login.serverInfomation')">
                        </el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogConfigVisible = false">
                        {{$t('global.cancel')}}
                    </el-button>
                    <el-button type="primary" @click="saveConfig()">
                        {{$t('global.save')}}
                    </el-button>
                </div>
            </el-dialog>
        </div>
        <div class="caseNub"> &copy; 1983-2021 Advantech Co., Ltd.</div>
        </div>
    </div>
</template>

<script>
   
    import {loginApi, getAHostname} from '../restfulapi/authApi'
    import handleResponse from "../restfulapi/handleResponse"
    import utils from "../../assets/js/utils"
    import {setSession, setLocal, getLocal, removeLocal, checkLocal} from "../../assets/js/storage"
    import {applicationTitle} from "../../assets/js/constant"
    import {getOsTypeApi} from "../restfulapi/installStatusApi"
    import {getQrcodeApi, saveServerConfigApi, getAgentInfoApi} from "../restfulapi/configApi"
    import qrCode from '@chenfengyuan/vue-qrcode';
    export default {
        
        name: 'login',
        data() {
            const validateVerifycode = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(this.$t('login.verifycodeEmpty')))
                } else if (value.toLowerCase() !== this.identifyCode.toLowerCase()) {
                    callback(new Error(this.$t('login.verifycodeError')))
                } else {
                    callback()
                }
            }

            let validateLogin = (rule,value,callback)=>{
                let reg=new RegExp(/^[0-9A-Za-z]{3,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifySimpleError')))
                }else{callback()};
            };
            let username="";
            let password="";
            let checked=false;
            let host="";

            if(checkLocal("aimlinkData")){
                try{
                    let aimlinkData = JSON.parse(getLocal("aimlinkData"))
                    if(aimlinkData.isRemember){
                        checked = aimlinkData.isRemember;
                        username = aimlinkData.username;
                        password = utils.decrypt(aimlinkData.password);
                    }
                }catch(e){
                    console.error(e);
                }
                
            }
            
            return {
                title: '',
                appTitle: applicationTitle,
                loading: false,
                redirectUrl: '',
                form: {
                    username: username,
                    password: password,

                },

                rules2: {
                    username: [
                        { required: true, trigger: 'blur', validator: validateLogin}
                    ],
                    password: [
                        { required: true, trigger: 'blur', validator: validateLogin}
                    ]
                },
                checked: checked,
                qrCodeValue: "",
                qrIsShow: false,
                agent_switch: "qrcode",

                configForm: {
                    configValue: ""
                },
                dialogConfigVisible: false,
                configLoading:false,

                serverip: "",
                agentVersion: "",
                agentStatus: "",
                deviceName: ""
            }
        },

        components: {
            qrCode,
        },

        methods: {
            handleSubmit() {
                if(this.form.username){
                    this.loading = !this.loading
                    loginApi(this.form.username, this.form.password, this.form.url).then((data) => {
                        this.loading = false;
                        handleResponse(data, (res) => {
                            if(res.status == "success"){
                                let aimlinkData = {};
                                aimlinkData.username = this.form.username;
                                aimlinkData.password = utils.encrypt(this.form.password);
                                if(this.checked){
                                    aimlinkData.isRemember = this.checked;
                                    setLocal("aimlinkData", aimlinkData);
                                }else{
                                    removeLocal("aimlinkData");
                                }
                                setSession("aimlinkData", aimlinkData);
                                cookie.setCookie("eideviceToken", res.data.token, 60);
                                setSession("username", this.form.username);
                                let nowTime = this.$moment(new Date().getTime()).format("YYYY-MM-DD HH:mm:ss");
                                setSession("logintime", nowTime);
                                this.$router.replace({name:'main'});
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                }
            },
            refreshIdentify(){
                var str = "",
                arr = ['0', '2', '3', '4', '5', '8', '9', 'a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'k', 'm', 'n', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'y', 'z', 'A', 'B', 'D', 'H', 'G', 'Q', 'W','N', 'R', 'E'];
                for(var i=0; i<4; i++){
                    let pos = Math.round(Math.random() * (arr.length-1));
                    str += arr[pos];
                }
                this.identifyCode = str;
            },
            randomWord(count){
                return str;
            },

            getOs(){
                getOsTypeApi().then((res) => {
                    handleResponse(res, (res) => {
                        if(res.status == "success"){
                            let os = res.data;
                            setLocal("agent-os", os);
                        }
                    })
                })
            },

            getAgentInfo(){
                getAgentInfoApi().then((res) => {
                    handleResponse(res, (res) => {
                        if(res.status == "success"){
                            let agentInfo = JSON.parse(res.data);
                            this.agentVersion = agentInfo.agentversion;
                            this.serverip = agentInfo.serverip;
                            this.agentStatus = agentInfo.agentstate;
                            this.deviceName = agentInfo.agentname;
                        }
                    })
                })
            },

            getQrcode(){
                getQrcodeApi().then((obj) => {
                    handleResponse(obj, (res) => {
                        if(res.status == "success"){
                            this.qrCodeValue = res.data;
                            this.qrIsShow = true;
                        }
                    })
                })
            },

            saveConfig(){
                this.configLoading = true;
                saveServerConfigApi(this.configForm).then((obj) => {
                    this.configLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "success"){
                            this.dialogConfigVisible = false;
                            this.$message({
                                message: 'Success',
                                type: 'success'
                            });
                        }else{
                            _g.handleError(res);
                        }
                    })
                })
            }
            
        },
        created(){
            this.getOs();
            this.getQrcode();
            this.getAgentInfo();
            // this.refreshIdentify();
        },
        
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/colors";
    @import '../../../static/ensaas_login/libs/bootstrap/css/bootstrap.min.css';
    @import '../../../static/ensaas_login/css/wisepaas-login_template.css';
    @import '../../../static/ensaas_login/css/wisepaas-login_template_login.css';
    #center-logo{
        width: 210px;   
        height:40px;
        margin-top:15px;
    }
    .verify-pos {
        position: absolute;
        right: 100px;
        top: 0px;
    }
    .card-box {
        padding: 20px;
        /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
        -webkit-border-radius: 5px;
        border-radius: 5px;
        -moz-border-radius: 5px;
        background-clip: padding-box;
        margin-bottom: 20px;
        background-color: #F9FAFC;
        margin: 120px auto;
        width: 400px;
        border: 2px solid #8492A6;
        .login_btn{
            // background: #1A82C5;
            background: linear-gradient(to right top,  #1382c6,#004889)
        }
    }

    .title {
        margin: 0px auto 40px auto;
        text-align: center;
        img{
            margin-top: 20px;
            width: 210px;
        }
        h1{
            margin-bottom:5px;
            color: $primary-color;
        }
        p{
            color: $darkgray-color;
        }
    }

    .loginform {
        width: 350px;
        padding: 35px 35px 15px 35px;
    }
    .identifyInput{
        border-radius: 4px;
        border: 1px solid #dcdfe6;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        color: #606266;
        display: inline-block;
        font-size: inherit;
        height: 40px;
        outline: 0;
        padding: 0 15px;
        -webkit-transition: border-color .2s cubic-bezier(.645,.045,.355,1);
        -o-transition: border-color .2s cubic-bezier(.645,.045,.355,1);
        transition: border-color .2s cubic-bezier(.645,.045,.355,1);
        width: 200px;
        vertical-align: top;
        margin-right: 6px;
    } 
    input::-webkit-input-placeholder{
        color: #c0c5d7;
    }
    ::-moz-placeholder {
        color: #c0c5d7;
    }
    .refresh{
        color: #c0c5d7;
        vertical-align: middle;
        margin-left: 5px;
        margin-bottom: 22px;
        cursor: pointer
    }

    #hostname{
        width: 320px;
        height: 40px;
        margin: 0 auto;
        border-radius: 0px;
    }
    .qrcode{
        position: absolute;
        top: 160px;
        width: 100%;
        // right:75px;
        text-align: center;
        .qrIntroduce {
            font-family: NotoSans;
            font-weight: 400;
            color: #342e2d;
            text-align: center;
        }
    }
    .switch{
        position: absolute;
        top: 35px;
        right:20px; 
        .switch-label{
            margin-right:5px;
            font-weight: 600px;
            line-height: 24px;
        }
        .el-switch{
            display: inline-block !important;
            position: relative;
            top: -2px;
        }
    }

    .input-config{
        font-weight: 400;
        color: #342e2d;
    }

    .url{
        color: #2361ae;
        cursor: pointer;
    }
    .url:hover{
        color: #2361ae;
        text-decoration: underline;
    }

    .logo-icon{
        color: #1299D8;
        font-size: 42px;
        font-weight: 900;
    }
    .agentInfo{
        label{
            margin-right: 10px;
            font-weight: 700;
            width: 120px;
        }
    }
    .agentVersion{
        width: 100%;
        text-align: center;
        position: absolute;
        bottom: 10px;
        font-size: 14px;
        opacity: .8;
        i{
            position: relative;
            top:2px;
        }
    }

    .agentInfo{
        .agentInfoItem{
            margin: 0 !important;
            padding: 0 !important;
        }
    }

    .server{
        margin-top: 10px;
        .server-label{
            font-weight: 500;
            margin-right: 10px;

        }

    }

    @media screen and (max-width:1440px){
        .switch{
            right: 5px;
        }
        .server{
            margin-top: 0 !important;
        }

        #center-logo-wrapper{
            top: 63px !important;
        }
        .qrcode{
            top: 120px;
        }
        p{
            margin-bottom: 0 !important;
        }
    }

</style>


